import React from 'react';

class AddCompany extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            company:{
                title: '',
                categories: '',
                content: ''
            },
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(fieldName) {
        return ((event) => {
            let company = this.state.company;
            company[fieldName] = event.target.value;
            this.setState({company: company});
        } );
    }

    handleSubmit(event) {
        console.log('A company was submitted: ' + JSON.stringify(this.state.company));
        event.preventDefault();

        // prepare data for edit profile API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("Content-Type", "application/json");

        // Send form to submit app API and get response
        //fetch('http://vps432643.ovh.net:3001/api/company/new', {
        fetch('https://reduxblog.herokuapp.com/api/posts?key=123turki', {
            method: 'POST',
            headers: myHeaders,
            body: JSON.stringify(this.state.company),
        })
            .then(function (response) {
                return response.json();
            })
            .then((jsonData) => {
                console.log("/api/company/new' POST then", jsonData);
                this.props.notify('Entreprise ajoute avec succs','info');
                this.props.refreshCompaniesList();
            })
            .catch(err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                this.props.notify('Operation termin avec erreur ','error');
                console.log("/api/company/new' POST catch", err);
            });
    }

    handleSubmitTestUpdate(event) {
        //console.log('A company was submitted: ' + JSON.stringify(this.state.company));
        event.preventDefault();

        // prepare data for edit profile API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("x-access-token", sessionStorage.getItem('token'));

        let form = new FormData(document.getElementById('add_company_form'));
        // Send form to submit app API and get response
        fetch('http://vps432643.ovh.net:3001/api/company/update/5aaa4833bc09ff1bc46fea62', {
            method: 'PUT',
            headers: myHeaders,
            body: form,
        })
            .then(function (response) {
                return response.json();
            })
            .then((jsonData) => {
                console.log("/api/company/update/5aaa4833bc09ff1bc46fea62", jsonData);
                this.props.refreshCompaniesList();
            })
            .catch(err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                console.log("/api/company/update/5aaa4833bc09ff1bc46fea62", err);
            });
    }

    render(){
        return (
            <div className="card">
                <div className="card-body">
                    <button onClick={this.props.hideAddCompanyForm} type="button" className="btn btn-default btn-circle btn-remove"><i className="fa fa-remove"/></button>
                    <form id="add_company_form" className="row" onSubmit={this.handleSubmit}>
                        <div className="col-md-6">
                            <h5 className="card-title"> Ajouter post </h5>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Titre</label>
                                <div className="col-sm-8">
                                    <input required name="code" type="text" className="form-control" value={this.state.company.title} onChange={this.handleChange('title')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Catgories</label>
                                <div className="col-sm-8">
                                    <input required name="name" type="text" className="form-control" value={this.state.company.categories} onChange={this.handleChange('categories')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Contenu</label>
                                <div className="col-sm-8">
                                    <input name="address" type="text" className="form-control" value={this.state.company.content} onChange={this.handleChange('content')} />
                                </div>
                            </div>

                            <div className="form-group row">
                                <div className="col-sm-8">
                                    <button type="submit" className="btn btn-primary">Ajouter</button>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default AddCompany;


